.loading-marquee-skelton-background {
    display: flex;
    flex-direction: column;
    height: 484px;
    padding: 0px 64px;
    margin-bottom: -5px;
    background: linear-gradient(180deg, rgba(5, 42, 27, 0.02) 46.27%, #05112A 90.29%), linear-gradient(96.37deg, #05112A 0.93%, #165C73 70.32%);

    @media (min-width: 1280px) and (max-width: 1439px) {
        height: 456px;
        padding: 0px 64px;
    }

    @media screen and (min-width: 834px) and (max-width: 1194px) and (orientation: landscape) {
        height: 362px
    }

    @media screen and (min-width: 834px) and (max-width: 1194px) and (orientation: portrait) {
        height: 408px
    }

    @media screen and (min-width: 415px) and (max-width: 833px) {
        height: 296px
    }

    @media (min-width: 375px) and (max-width: 414px) {
        height: 296px;
        padding: 0px 31px;
    }

    @media screen and (max-width: 375px) {
        height: 296px
    }
}

.loading-header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding-bottom: 64px;
    // padding: 10px 25px 16px 72px;

    @media (min-width: 375px) and (max-width: 414px) {
        padding: 0px 0px 26px 0px;
    }
}

.loading-header-profile-icon {
    display: flex;
    justify-content: flex-end;
    width: 22%;



    @media screen and (min-width:375px) and (max-width:414px) {
        justify-content: space-between
    }

    @media screen and (max-width:375px) {
        justify-content: space-between
    }

    .profile2 {
        @media screen and (min-width:834px) and (max-width:1440px) {
            display: none;
        }
    }
}

.loading-header-logo {
    &.MuiSkeleton-root {
        width: 129px;
        height: 33px;
        left: 0px;
        top: 0.5px;
        background: rgba(247, 249, 253, 0.1);
    }
}

.loading-header-profile {
    &.MuiSkeleton-root {
        width: 32px;
        height: 32px;
        border-radius: 50%;
        background: rgba(247, 249, 253, 0.1);
    }
}

.loading-marquee-skelton-wrapper {
    display: flex;
    flex-direction: row;
    flex-grow: 1;
    justify-content: center;
    align-items: center;

    @media screen and (min-width:834px) and (max-width:1280px) {
        justify-content: left;
    }

}

.loading-marquee-titletext {
    &.MuiSkeleton-root {
        width: 420px;
        height: 55px;
        background: rgba(247, 249, 253, 0.1);

        @media screen and (min-width: 834px) and (max-width: 1194px) and (orientation: portrait) {
            width: 281px;
        }

        @media screen and (max-width: 414px) {
            width: 343px;
        }

        @media screen and (max-width: 375px) {
            width: 313px;
        }
    }
}

.loading-marquee-image-wrapper {
    display: flex;
    flex-grow: 2;
    justify-content: flex-end;
    align-items: center;
}

.loading-marquee-image {

    &.MuiSkeleton-root {
        width: 90%;
        height: 352px;
        background: rgba(247, 249, 253, 0.1);
        border-radius: 8px;

        @media screen and (min-width:834px) and (max-width:1194px) and (orientation:portrait) {
            width: 367px;
            height: 235px;
        }

        @media screen and (min-width:834px) and (max-width:1194px) and (orientation:landscape) {
            height: 235px;
        }

        @media screen and (min-width:260px) and (max-width:414px) {
            display: none;
        }
    }
}

.loading-marquee-description {
    &.MuiSkeleton-root {
        width: 210px;
        height: 29px;
        background: rgba(247, 249, 253, 0.1);

        @media screen and (max-width: 414px) {
            width: 171.5px;
        }

        @media screen and (max-width: 375px) {
            width: 156px;
        }
    }
}


.loading-marquee-button {
    &.MuiSkeleton-root {
        width: 158px;
        height: 40px;
        background: rgba(247, 249, 253, 0.1);
        border-radius: 40px;
        margin-top: 24px;

        @media screen and (max-width: 414px) {
            width: 129.03px;
        }

        @media screen and (max-width: 375px) {
            width: 118px;
        }
    }
}

.loading-library-title {
    &.MuiSkeleton-root {
        width: 39%;
        height: 40px;
        background: rgba(247, 249, 253, 0.1);
        margin-bottom: 24px;

        @media (min-width: 375px) and (max-width: 414px) {
            width: 62%;
            height: 24px;
        }
    }
}

.loading-library-container {
    display: 'grid';
    gap: '24px';
    grid-template-columns: repeat(auto-fill, minmax(169px, 1fr));

    @media screen and (min-width: 375px) and (max-width: 833px) and (orientation: portrait) {
        grid-template-columns: repeat(auto-fill, minmax(160px, 1fr));
    }
}

.library {
    display: grid;
    gap: 16px;
    grid-template-columns: repeat(auto-fill, minmax(169px, 1fr));
    margin-bottom: 40px;

    @media screen and (min-width: 375px) and (max-width: 1280px) {
        gap: 16px;
        grid-template-columns: repeat(auto-fill, minmax(160px, 1fr));
        margin-bottom: 40px
    }
}

.loading-library-card {
    &.MuiSkeleton-root {
        /* item */
        height: 219px;
        background: rgba(247, 249, 253, 0.1);
        border-radius: 8px;

        @media screen and (min-width: 834px) and (max-width: 1194px) {
            height: 160px;
        }

        @media (min-width: 375px) and (max-width: 414px) {
            height: 160px
        }
    }

    .librarycard3 {
        @media screen and (min-width: 375px) and (max-width: 1194px) and (orientation: portrait) {
            display: none;
        }
    }

    .librarycard4 {
        @media screen and (min-width: 375px) and (max-width: 1194px) and (orientation: portrait) {
            display: none;
        }

    }
}



.loading-offers-card {
    &.MuiSkeleton-root {
        /* item */
        height: 202px;
        background: rgba(247, 249, 253, 0.1);
        border-radius: 8px;

        @media screen and (min-width: 834px) and (max-width: 1194px) and (orientation: portrait) {
            height: 236px;
        }

        @media screen and (min-width: 834px) and (max-width: 1194px) and (orientation: landscape) {
            height: 248px;
        }

        @media screen and (min-width: 375px) and (max-width: 414px) {
            display: none;
        }
    }


}


.loading-channels-wrapper {
    display: grid;
    gap: 16px;
    grid-template-columns: repeat(3, 1fr);
    margin-bottom: 40px;

    @media screen and (max-width: 1280px) {
        grid-template-columns: repeat(4, 1fr);
    }

    @media screen and (min-width: 834px) and (max-width: 1194px) and (orientation: portrait) {
        grid-template-columns: repeat(2, 1fr);
    }
    @media screen and (min-width: 834px) and (max-width: 1194px) and (orientation: landscape) {
        grid-template-columns: repeat(3, 1fr);
    }

    @media screen and (min-width: 375px) and (max-width: 414px) {
        grid-template-columns: repeat(1, 1fr);
    }

    .channelcard3 {
        @media screen and (min-width: 834px) and (max-width: 1194px) and (orientation: portrait) {
            display: none;
        }

        @media screen and (min-width: 375px) and (max-width: 414px) {
            display: none;
        }

    }

    .channelcard4 {
        display: none;
        @media (min-width: 1195px) and (max-width: 1280px) {
            display: block;
        }
    }


    .loading-channels-card {
        &.MuiSkeleton-root {
            /* item */
            height: 245px;
            background: rgba(247, 249, 253, 0.1);
            border-radius: 8px;

            @media screen and (min-width: 834px) and (max-width: 1194px) and (orientation: portrait) {
                height: 256px;
            }

            @media screen and (min-width: 834px) and (max-width: 1194px) and (orientation: landscape) {
                height: 248px;
            }
        }
    }

}

.loading-channels-title {
    &.MuiSkeleton-root {
        width: 23%;
        height: 40px;
        background: rgba(247, 249, 253, 0.1);
        margin-bottom: 24px;

        @media screen and (min-width: 375px) and (max-width: 1194px) and (orientation: portrait) {
            width: 38%
        }

        @media (min-width: 375px) and (max-width: 414px) {
            width: 56%;
            height: 24px;
        }
    }
}

.loading-library-button {
    display: none;

    @media screen and (min-width: 375px) and (max-width: 414px) {
        &.MuiSkeleton-root {
            width: 129.03px;
            height: 40px;
            /* primary/tinted-white */

            background: rgba(247, 249, 253, 0.1);
            border-radius: 40px;
            margin-bottom: 24px;
            left: 29%
        }
    }
}

.library-card-content {
    grid-column: span 2;
}

.librarycard1 {
    @media screen and (min-width: 375px) and (max-width: 414px) {
        grid-column: span 2;
    }
}

.librarycard2 {
    @media screen and (min-width: 375px) and (max-width: 414px) {
        grid-column: span 2;
    }
}

.loading-offers-title {
    &.MuiSkeleton-root {
        width: 302px;
        height: 40px;
        background: rgba(247, 249, 253, 0.1);
        margin-bottom: 24px;
    }

    @media screen and (min-width: 375px) and (max-width: 414px) {
        display: none;
    }
}

.loading-container {
    padding: 0px 64px;
    background: #05112A;

    @media screen and (max-width: 814px) {
        padding: 0px 31px;
    }

    @media screen and (max-width: 414px) {
        padding: 0px 31px;
        position: relative;
        bottom: 25px;
    }

    @media screen and (max-width: 375px) {
        padding: 0px 31px;
        position: relative;
        bottom: 25px;
    }

    .librarycard3 {
        @media screen and (min-width: 375px) and (max-width: 414px) {
            display: none
        }

        @media screen and (min-width: 375px) and (max-width: 1194px) and (orientation: portrait) {
            display: none;
        }

    }

    .librarycard4 {
        @media screen and (min-width: 375px) and (max-width: 1194px) and (orientation: portrait) {
            display: none
        }
    }
}

.loading-offers-container {
    .loading-offers-wrapper {
        display: grid;
        gap: 16px;
        grid-template-columns: repeat(2, 1fr);
        margin-bottom: 40px;

        @media screen and (min-width: 375px) and (max-width: 414px) {
            display: none;
        }

        @media screen and (min-width: 834px) and (max-width: 1194px) and (orientation: landscape) {
            grid-template-columns: repeat(4, 1fr);
        }

        @media screen and (max-width: 413px) {
            grid-template-columns: repeat(4, 1fr);
        }

    }

    @media screen and (min-width: 375px) and (max-width: 414px) {
        display: none;
    }
}