/**
* Glossary popup styles
*
* @file glossaryPopup.css
* @author Prithviraj K
*/

.glossary-audioIcon {
  cursor: pointer;
  float: right;
  height: 28px;
  width: 28px;
}

#glossary-popper {
  background-color: #ffffff;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.4);
  border-radius: 4px;
  border: 1px solid #e9e9e9;
  padding: 15px;
  width: 400px;
  z-index: 99;
}

#glossary-popper::before {
  content: "";
  border: 15px solid transparent;
  position: absolute;
  left: 185px;
}

#glossary-popper[x-placement*="top"]::before {
  border-top-color: #f5f5f5;
  bottom: -30px;
}

#glossary-popper[x-placement*="bottom"]::before {
  border-bottom-color: #f5f5f5;
  top: -30px;
}

.glossary-term {
  margin: 0 0 5px 0;
  font-size: 16px;
  font-weight: 700;
  text-align: left;
  line-height: 1.22;
  letter-spacing: -0.5px;
  color: #252525;
  word-wrap: break-word;
}

.glossary-definition {
  word-break: break-word;
  line-height: 1.38;
  font-size: 16px;
  color: #252525;
  letter-spacing: -0.4px;
  margin: 5px 0;
  text-align: left;
}

