#profile-dropdown-icon-button-avatar {
  background: linear-gradient(0deg, #002E60 17.63%, #005D83 71.38%, #007A98 115.91%);
  background: -webkit-linear-gradient(0deg, #002E60 17.63%, #005D83 71.38%, #007A98 115.91%);
  background: -moz-linear-gradient(0deg, #002E60 17.63%, #005D83 71.38%, #007A98 115.91%);
  background: -ms-linear-gradient(0deg, #002E60 17.63%, #005D83 71.38%, #007A98 115.91%);
  color: #ffffff;
}

#profile-dropdown-indicator-open,
#profile-dropdown-indicator-close {
  color: #6A7070;
}

.maincontainer {
  height: 100%;
  position: relative;
  margin-top: -180px;
  z-index: 999;

  @media screen and (max-width:374px) {
    margin-top: 0%;
  }

  @media screen and (min-width:375px) and (max-width:414px) {
    margin-top: 0%;
  }

  @media screen and (min-width:415px) and (max-width:600px) {
    margin-top: -28%;
  }

  @media screen and (min-width:601px) and (max-width:768px) {
    margin-top: -15%;
  }


  @media screen and (min-width:769px) {
    margin-top: -5%;
  }

  @media screen and (min-width:1024px) {
    margin-top: -15%;
  }

  @media screen and (min-width:1100px) {
    margin-top: -15%;
  }

  @media screen and (min-width: 1200px) {
    margin-top: -9%;
  }

  @media screen and (min-width: 1280px) {
    margin-top: -130px;
  }


  @media screen and (min-width: 1720px) {
    margin-top: -6.6%;
  }


  @media screen and (min-width: 1380px) and (max-width: 1439px) {
    margin-top: -186px;
  }

  @media screen and (min-width: 1550px) and (max-width: 1719px) {
    margin-top: -11.6%;
  }

  @media screen and (min-width:1195px) and (max-width:1279px) {
    // margin-top: -125px;
    margin-top: -10.6%;
  }

  @media screen and (min-width:834px) and (max-width:1194px) and (orientation:portrait) {
    // margin-top: -125px;
    margin-top: -9%;
  }

  @media screen and (min-width:834px) and (max-width:1194px) and (orientation:landscape) {
    // margin-top: -125px;
    margin-top: -14%;
  }

}

.offers-subtitle {
  font-family: Hind;
  font-style: normal;
  font-weight: normal;
  font-size: 22px;
  line-height: 120%;
  color: #F7F9FD;
  margin-bottom: 24px;
  margin-top: 8px;
}

.offers-title {
  line-height: 48px;
  font-family: TT Commons;
  font-size: 32px;
  color: #F7F9FD;
  line-height: 100%;
  margin: 10px 75px 0px 75px;
}

.offersContainer {
  margin-top: 24px;
}

.offerDetailsPage {
  margin: 0 auto;
  padding-top: 7%;
}

.breadcrums {
  margin-bottom: 20px !important;
  margin-top: 20px !important;
}

.backtohome span {
  font-family: TT Commons;
  font-size: 18px;
  color: #F7F9FD;
}

.expandedContainer {
  position: relative;
  z-index: 4;
  padding-top: 4.2%;

  @media screen and (max-width:375px) {
    padding-top: 33%;
  }

  @media screen and (min-width:376px) and (max-width:414px) {
    padding-top: 24%;
  }

  @media screen and (min-width:415px) and (max-width:600px) {
    padding-top: 15%;
  }

  @media screen and (min-width:601px) and (max-width:750px) {
    padding-top: 12%;
  }

  @media screen and (min-width:751px) and (max-width:850px) {
    padding-top: 9%;
  }

  @media screen and (min-width:851px) and (max-width:1280px) and (orientation:portrait) {
    padding-top: 6%;
  }

  @media screen and (min-width:851px) and (max-width:1280px) and (orientation:landscape) {
    padding-top: 6%;
  }

  @media screen and (min-width: 1280px) and (max-width: 1440px) {
    padding-top: 5.6%;
  }
}

.libraryExpand,
.breadcrums {
  margin: 0px 64px 0px 64px;

  @media screen and (max-width: 499px) {
    margin: 0px 16px 0px 16px;
  }

  @media screen and (min-width:500px) and (max-width: 1439px) {
    margin: 0px 32px 0px 32px;
  }
}

.divider {
  outline: 1px solid #4F5561;
  margin: 0px 64px 48px 64px;
  display: flex;

  @media screen and (max-width: 499px) {
    margin: 20px 16px;
  }

  @media screen and (min-width:500px) and (max-width: 1439px) {
    margin: 20px 32px;
  }

}