.shape {
    bottom: 131px;
    height: 300px;
    background: transparent;
    width: 100%;
    height: 154px;
    z-index: 2;
    position: absolute;

    @media screen and (max-width: 1180px) {
        bottom: 200px;
    }
}

.shape::after {
    content: "";
    display: block;
    width: 204%;
    height: 288%;
    position: absolute;
    border-radius: 37%;
    bottom: 0;
    right: -18%;
    box-shadow: 0px 280px 0 0 #f7f9fd;

 

    @media screen and (min-width: 834px) and (max-width: 1194px) and (orientation: portrait) {
        display: none
    }

    @media screen and (min-width: 834px) and (max-width: 1194px) and (orientation: landscape) {
        display: none
    }

    @media screen and (max-width: 834px) {
        display: none
    }
}

.footerDiv::before {
    content: '';
    position: absolute;
    background: linear-gradient(94deg, rgba(255, 255, 255, 0.1) .71%, #fff 0%, #fff 0%, rgba(255, 255, 255, 0) 100.32%);
    transform: rotate(253deg);
    filter: opacity(0.05);
    width: 24%;
    padding-top: 33%;
    bottom: 0%;
    left: 3%;
    z-index: 2;



    @media screen and (min-width: 834px) and (max-width: 1194px) and (orientation: portrait) {
        display: none
    }

    @media screen and (max-width: 834px) {
        display: none
    }
}

.footerDiv {
    background: #05112A;
    position: relative;
    display: flex;
    height: 482px;
    margin-top: 100px;

    @media screen and  (max-width: 833px) {
        height: 600px;
    }
    @media screen and (min-width:834px) and (max-width: 1280px) {
        margin-top: 0;
        height: 525px;
    }
}

.downloadAppIcon {
    padding: 30px 72px 0px 0px;
    padding-top: 40px;


    @media screen and (max-width: 834px) {
        padding: 55px 0px 69.71px 0px;
        margin-left: 24px;
    }

    @media screen and (min-width: 834px) and (max-width: 1194px) and (orientation: portrait) {
        padding: 30px 0px 30px 0px;
    }

    @media screen and (min-width: 834px) and (max-width: 1194px) and (orientation: landscape) {
        padding: 30px 0px 30px 0px;
    }

    @media screen and (min-width: 1280px) and (max-width: 1439px) {
        padding: 38px 0px 0px 0px;
    }

    @media screen and (max-width:360px) {
        padding-top: 0
    }
}

.footerIllustration {
    width: 463px;
    height: 306px;
    position: absolute;
    right: 150px;
    top: 29px;

    @media screen and  (max-width: 833px) {
        right: 0;
        top: 0;
    }

    @media (min-width:834px) and (max-width: 1280px) and (orientation: landscape) {
        position: absolute;
        right: 112px;
    }
    @media (min-width:1195px) and (max-width: 1280px) {
        top: 75px;
    }

    @media (min-width:834px) and (max-width: 1194px) and (orientation: portrait) {
        position: absolute;
        right: 64px;
    }

}