body {
  padding: 0;
  margin: 0;
  font-family: "Open Sans", sans-serif;
  background-color: #ffffff;
}
.circleStyle {
  position: absolute;
  top: 50%;
  left: 50%;
}
.navigation-disabled {
  opacity: 0.5;
  pointer-events: none;
}
.offer-link-li {
  align-self: center;
   margin-top: 4px;
}
.BG-White .offers-link-icon:hover  {
  background-color: #E8EEFA;
}
.BG-Akaroa .offers-link-icon:hover  {
  background-color: #1C222E25
}
.BG-Black .offers-link-icon:hover  {
  background-color: #333333
}
/* Tooltip color change based on preference theme selected */
#offers-link-tooltip div{
  background-color: #05112A;
  color: #FEFEFE;
}
#offers-link-tooltip div span{
  color: #05112A;
}
#offers-link-tooltip div p{
  color: #FEFEFE;
}
.vega-preference-theme-Black #offers-link-tooltip div{
  background-color: #F7F9FD;
  color: #333333;
}
.vega-preference-theme-Black #offers-link-tooltip div span{
  color: #F7F9FD;
}
.vega-preference-theme-Black #offers-link-tooltip div p{
  color: #333333;
}

/* Overriding foxit Progress bar styles for audio playlist */
.navigation-audio-player .progress-bar {
  background-color: #ffffff;
  line-height: inherit;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  box-shadow: none;
}

@keyframes shake {
  0% { margin-left: 0rem; border: 2px solid #7E031A;}
  25% { margin-left: 0.5rem; border: 2px solid #7E031A;}
  75% { margin-left: -0.5rem; border: 2px solid #7E031A;}
  100% { margin-left: 0rem; border: 2px solid #7E031A;}
}

@media print {
  html,
  body {
    display: none;
  }
}

