/**
* otpDialog styles
*
* @file OTPDialog.scss
* @author Poornima N
*/

.OtpSmsTitle {
    position: absolute;
    display:flex;
    justify-content: space-around;
    width: 90%;
    height: 33px;
    top: 40px;
    font-family: 'TT Commons';
    font-style: normal;
    font-weight: 700;
    font-size: 30px;
    line-height: 110%;
    color: #000000;
}

.otpSmsTitleLocale {
    position: absolute;
    display:flex;
    justify-content: space-around;
    width: 90%;
    height: 33px;
    font-family: 'TT Commons';
    font-style: normal;
    font-weight: 700;
    font-size: 30px;
    line-height: 110%;
    color: #000000;
    left:35px;
}

.otpSmsDesc {
    position: absolute;
    height: 21px;
    left: calc(50% - 90px/2 - 131.5px);
    top: 105px;
    font-family: 'Hind';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    color: #333333;
}

.closeButtonOtp {
    width: 14px;
    height: 14px;
    padding: 0;
    position: absolute !important;
    top: 26px;
    right: 24px
}

.dividerClass {
    position: absolute !important;
    width: 393px;
    height: 0px;
    left: 16px;
    top: 89px;
    border: 1px solid #E8EEFA
}

.dialogactionOtp {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 4px 20px;
    position: absolute;
    width: 118px;
    height: 40px;
    top: 108px;
    left: 261px;
    background: linear-gradient(135deg, #EE512E 0%, #DF006B 63.44%, #BD0071 100%);
    border-radius: 40px
}

.sendCode {
    font-family: 'Hind';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 150%;
    color: #FEFEFE
}

.sendCodeLocale {
    font-family: 'Hind';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 15px;
    color: #FEFEFE
}

.sendCodeBtn {
    font-family: 'Hind';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 150%;
    color: #FEFEFE
}

.iconButton {
    font-size: 9px;
    padding-top: 11px;

    & .MuiIconButton-root {
        color: #05112A
    }
}

.countryContainer {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 11px 12px;
    position: absolute;
    width: 219px;
    height: 44px;
    left: 36px;
    top: 130px;
    background: #FEFEFE;
    box-sizing: border-box;
    border-radius: 4px;
    border: 1px solid #707D89;
}

.sentOtp {
    border: 2px solid #00796D;
}

.invalidCountryContainer {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 11px 12px;
    position: absolute;
    width: 219px;
    height: 44px;
    left: 36px;
    top: 130px;
    background: #FEFEFE;
    box-sizing: border-box;
    border-radius: 4px;
    border: 2px solid #CA4644
}

.selectHr {
    position: absolute;
    width: 33px;
    left: 66px;
    border: 1px solid #9BA1AD;
    transform: rotate(90deg);
    flex: none
}

.selectInput {
    position: static !important;
    width: 122px;
    left: calc(50% - 122px/2 + 36.5px);
    top: 22.73%;
    bottom: 22.73%;
    padding-left: 10px !important;
    & .MuiInputBase-fullWidth {
        width: 122px;
    }
}

.errorHelperText {
    position: absolute;
    width: 186.88px;
    height: 21px;
    left: calc(50% - 52.88px/2 - 83.06px);
    top: 46px;
    font-family: 'Hind';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    color: #CA4644;
}

.successHelperText {
    position: absolute;
    width: 186.88px;
    height: 21px;
    left: calc(50% - 52.88px/2 - 83.06px);
    top: 46px;
    font-family: 'Hind';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    color: #00796D;
}

.phoneSelect {
    &:focus {
        background: transparent
    }
}

.phoneSelectIcon {
    position: absolute;
    right: 8px;
    color: #1C222E;
    top: calc(50% - 9px);
}

.selectRoot {
    font-weight: 700
}

.menuItemClass {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    align-items: baseline;
}

.menuItemText {
    font-family: 'Hind';
    font-style: normal;
    font-size: 16px;
    line-height: 150%;
}

.menuItemRoot {
    &.MuiListItem-root.Mui-selected {
        font-weight: 700;
        background-color: #F7F9FD;
    }
    &:nth-last-child(1) {
        border-bottom: none
    }
}

.paperOtpPopover {
    position: absolute;
    width: 425px;
    height: 223px;
    left: 526px !important;
    top: 243px !important;
    background: #F7F9FD;
    border-radius: 16px;
}