.library-container {
    font-family: TT Commons;
    font-size: 40px;      
    color: #F7F9FD;
    line-height: 100%;   
    margin: 0px 64px 0px 64px;
    padding-bottom: 30px;
    &.offersContainer { 
      margin-top: 24px;
    }
    &.offers-title {
      color: #F7F9FD;
      font-size: 32px;
      font-family: TT Commons;
    }
    &.offers-subtitle {
      font-family: TT Commons;
      font-style: normal;
      font-weight: 300;
      font-size: 22px;
      line-height: 120%;
      color: #F7F9FD;
      margin-bottom: 16px;
    }
    @media screen and (max-width: 499px) {
      margin: 10px 16px 0px 16px;
    }
    @media screen and (min-width:500px) and (max-width: 1439px) {
      margin: 10px 32px 0px 32px;
    }
  }
  .offers-container {
    font-family: TT Commons;
    font-size: 32px;      
    color: #F7F9FD;
    line-height: 100%;   
    margin: 10px 64px 64px 64px;
    &.offersContainer { 
      margin-top: 24px;
    }
    &.offers-title {
      color: #F7F9FD;
      font-size: 32px;
      font-family: TT Commons;
    }
    &.offers-subtitle {
      font-family: TT Commons;
      font-style: normal;
      font-weight: 300;
      font-size: 22px;
      line-height: 24px;
      color: #F7F9FD;
      margin-bottom: 16px;
    }
    @media screen and (max-width: 499px) {
      margin: 10px 16px 0px 16px;
    }
    @media screen and (min-width:500px) and (max-width: 1439px) {
      margin: 10px 32px 0px 32px;
    }
}